import React, { useState } from 'react'
import { TEST_USER } from '../../Common/const'
import './BaseAuth.scss'

type Props = {
  setCurrentUser: React.Dispatch<React.SetStateAction<string | null>>
}

export const BaseAuth = ({ setCurrentUser }: Props) => {
  const [login, setLogin] = useState<string | null>()
  const [password, setPassword] = useState<string | null>()
  // Ошибка при авторизации
  const [errorAuth, setErrorAuth] = useState('')

  // Авторизация по логину и паролю
  const handlerPasswordAuth = () => {
    if (TEST_USER.login === login && TEST_USER.password === password) {
      setErrorAuth('')
      setCurrentUser(login)
    } else {
      setCurrentUser(null)
      setErrorAuth('Неправильный логин или пароль')
    }
  }

  return (
    <div onSubmit={handlerPasswordAuth} className='base-auth'>
      <input
        type='text'
        name='login'
        placeholder='login'
        className='base-auth-input'
        onChange={(event) => setLogin(event.target.value)}
      />
      <input
        type='text'
        name='password'
        placeholder='password'
        className='base-auth-input'
        onChange={(event) => setPassword(event.target.value)}
      />
      {errorAuth && <p className='base-auth-error'>{errorAuth}</p>}
      <button className='base-auth-btn' onClick={handlerPasswordAuth}>
        Войти
      </button>
    </div>
  )
}
