import React from 'react'
import './MainPage.scss'
import { create } from '@github/webauthn-json/browser-ponyfill'
import { passkeyType } from '../Common/type'
import { b64EncodeUnicode } from '../Common/helper'

type Props = {
  isAuthPasskey: boolean
  currentUser: string
  isPublicKeyForCurrentUser: (userName: string | null) => boolean
  setCurrentUser: React.Dispatch<React.SetStateAction<string | null>>
  passkeysArr: passkeyType[]
  setResultCreatePasskey: React.Dispatch<React.SetStateAction<string | undefined>>
  setPasskeysArr: React.Dispatch<React.SetStateAction<passkeyType[]>>
}

export const MainPage = ({
  isAuthPasskey,
  isPublicKeyForCurrentUser,
  currentUser,
  setCurrentUser,
  passkeysArr,
  setResultCreatePasskey,
  setPasskeysArr,
}: Props) => {
  const createNewPasskey = (userName: string) => {
    // в будущем userIdBase64 нужно заменить на индетификатор пользователя с бэкенда
    const userIdBase64 = b64EncodeUnicode(userName)
    const createOptions: CredentialCreationOptions = {
      // Объект, описывающий параметры создания учетных данных WebAuthn
      publicKey: {
        // Объект, описывающий проверяющую сторону, запросившую создание учетных данных
        rp: {
          name: 'Passkeys test page',
          id: 'alexanderpm.github.io',
        },
        // Объект, описывающий учетную запись пользователя, для которого создаются учетные данные
        user: {
          // `userIdBase64` is the user's passkey ID, from the database,
          // base64-encoded.
          id: Uint8Array.from(window.atob(userIdBase64), (c) => c.charCodeAt(0)),
          // `username` is the user's username. Whatever they would type
          // when signing in with a password.
          name: userName,
          // `displayName` can be a more human name for the user, or
          // just leave it blank.
          displayName: userName,
        },

        // This lists the ids of the user's existing credentials. I.e.
        //   SELECT id FROM passkeys WHERE username = ?
        // and supply the resulting list of values, base64-encoded, as
        // existingCredentialIdsBase64 here.
        excludeCredentials: passkeysArr.map((item) => {
          return {
            type: 'public-key',
            id: Uint8Array.from(atob(item.publicKey), (c) => c.charCodeAt(0)),
            transports: ['internal'],
          }
        }),

        // Boilerplate that advertises support for P-256 ECDSA and RSA
        // PKCS#1v1.5. Supporting these key types results in universal
        // coverage so far.
        pubKeyCredParams: [
          {
            type: 'public-key',
            alg: -7,
          },
          {
            type: 'public-key',
            alg: -257,
          },
        ],

        // Unused during registrations, except in some enterprise
        // deployments. But don't do this during sign-in!
        challenge: new Uint8Array([0]),

        authenticatorSelection: {
          authenticatorAttachment: 'platform',
          requireResidentKey: true,
          residentKey: 'required',
          userVerification: 'required',
        },

        // Сколько вызывающая сторона готова ждать завершение операции создания в миллисекундах
        timeout: 18000,
      },
    }

    create(createOptions).then(
      (pubKeyCredential) => {
        const response = pubKeyCredential
        if (response?.id) {
          setCurrentUser(userName)
          setPasskeysArr((oldArray) => [
            ...oldArray,
            { username: userName, publicKey: response.id },
          ])
          setResultCreatePasskey(JSON.stringify(response.toJSON()))
          alert(response.getClientExtensionResults())
        }
      },
      (response) => handleCreationError(response),
    )
  }

  function handleCreationError(e: Error) {
    if (e instanceof DOMException) {
      switch (e.name) {
        case 'InvalidStateError':
          console.log('InvalidStateError')
          return

        case 'NotAllowedError':
          console.log('NotAllowedError')
          return
      }
    }
    console.log(e)
  }
  // Logout
  const handlerLogout = () => {
    setCurrentUser(null)
  }
  const handlerCreatePasskey = () => {
    if (currentUser) {
      if (!isPublicKeyForCurrentUser(currentUser)) {
        createNewPasskey(currentUser)
      }
    }
  }
  return (
    <div className='main'>
      <p>Вы успешно авторизованы!</p>
      {isAuthPasskey && !isPublicKeyForCurrentUser(currentUser) && (
        <div className='main-passkey-create '>
          <p>
            Вам доступна авторизация по passkey! Для дальнейшего использования passkey для текущего
            пользователя нажмите кнопку использовать passkey!
          </p>
          <button onClick={handlerCreatePasskey} className='main-passkey-create-btn'>
            Использовать passkey
          </button>
        </div>
      )}
      <button onClick={handlerLogout} className='main-btn'>
        Выйти
      </button>
    </div>
  )
}
