import React, { useState } from 'react'
import './PasskeyAuth.scss'
import { b64EncodeUnicode } from '../../Common/helper'
import { get } from '@github/webauthn-json/browser-ponyfill'

type Props = {
  isPublicKeyForCurrentUser: (userName: string | null) => boolean
  setResultAuthPasskey: React.Dispatch<React.SetStateAction<string | undefined>>
  setCurrentUser: React.Dispatch<React.SetStateAction<string | null>>
}

export const PasskeyAuth = ({
  isPublicKeyForCurrentUser,
  setResultAuthPasskey,
  setCurrentUser,
}: Props) => {
  const [user, setUser] = useState<string>('')
  const [errorAuth, setErrorAuth] = useState<string>('')
  const handlerOnClick = () => {
    if (isPublicKeyForCurrentUser(user)) {
      // To abort a WebAuthn call, instantiate an `AbortController`.
      const abortController = new AbortController()

      const publicKeyCredentialRequestOptions: CredentialRequestOptions = {
        /*        mediation: 'conditional' as CredentialMediationRequirement,*/
        publicKey: {
          // Server generated challenge
          challenge: Uint8Array.from(atob(b64EncodeUnicode(user)), (c) => c.charCodeAt(0)),
          // The same RP ID as used during registration
          rpId: 'alexanderpm.github.io',
        },
        signal: abortController.signal,
      }
      get(publicKeyCredentialRequestOptions).then(
        (response) => {
          setResultAuthPasskey(JSON.stringify(response.toJSON()))
          setCurrentUser(user)
        },
        () => {
          alert('error')
        },
      )
    } else {
      setErrorAuth('Ключа для такого пользователя не существует')
    }
  }
  return (
    <div className='passkey-auth'>
      <input
        type='text'
        name='username'
        placeholder='email'
        autoComplete='username webauthn'
        onChange={(event) => setUser(event.target.value)}
      />
      {errorAuth && <p className='passkey-auth-error'>{errorAuth}</p>}
      <button onClick={handlerOnClick}>Далее</button>
    </div>
  )
}
