import React, { useEffect, useState } from 'react'
import './App.scss'
import { BaseAuth } from './Auth/BaseAuth/BaseAuth'
import { passkeyType } from './Common/type'
import { PasskeyAuth } from './Auth/PasskeyAuth/PasskeyAuth'
import { MainPage } from './Main/MainPage'
import { onServerFn } from './Server/OnServer'

function App() {
  // Проверяем, поддерживается ли авторизация по passkey
  const [isAuthPasskey, setIsAuthPassKey] = useState(false)
  const [currentUser, setCurrentUser] = useState<string | null>(null)
  const [passkeysArr, setPasskeysArr] = useState<passkeyType[]>([])
  const [userOpenAuthPasskey, setUserOpenAuthPasskey] = useState<boolean>(false)
  const [resultCreatePasskey, setResultCreatePasskey] = useState<string | undefined>()
  const [resultAuthPasskey, setResultAuthPasskey] = useState<string | undefined>()

  useEffect(() => {
    // Проверяем есть ли на устройстве локальный аутентификатор и будет ли браузер поддерживать пароли
    if (
      !window.PublicKeyCredential ||
      !(PublicKeyCredential as any).isConditionalMediationAvailable
    ) {
      setIsAuthPassKey(false)
      return
    }
    Promise.all([
      (PublicKeyCredential as any).isConditionalMediationAvailable(),
      // Статический метод, возвращающий Promise.
      // Разрешается в true если есть аутентификатор, привязанный к платформе, способен проверить пользователя.
      PublicKeyCredential.isUserVerifyingPlatformAuthenticatorAvailable(),
    ]).then((values) => {
      if (values.every((x) => x === true)) {
        setIsAuthPassKey(true)
      }
    })
  }, [])

  const isPublicKeyForCurrentUser = (userName: string | null) => {
    if (passkeysArr.length < 1 || !userName) {
      return false
    }
    let result = false
    passkeysArr.forEach((passkeys) => {
      if (passkeys.username === userName) {
        result = true
      }
    })
    return result
  }
  /*
  if (resultCreatePasskey !== undefined && resultAuthPasskey !== undefined) {
    onServerFn(resultCreatePasskey, resultAuthPasskey)
  }*/
  if (currentUser) {
    onServerFn('+', '-')
  }

  return (
    <div className='App'>
      <h2>Testing password and passkey authorization</h2>
      {/* Авторизация по логину и паролю */}
      {!currentUser && !userOpenAuthPasskey && <BaseAuth setCurrentUser={setCurrentUser} />}
      {!currentUser && isAuthPasskey && userOpenAuthPasskey && (
        <PasskeyAuth
          isPublicKeyForCurrentUser={isPublicKeyForCurrentUser}
          setResultAuthPasskey={setResultAuthPasskey}
          setCurrentUser={setCurrentUser}
        />
      )}
      {/* Успешная авторизация */}
      {currentUser && (
        <MainPage
          isAuthPasskey={isAuthPasskey}
          currentUser={currentUser}
          setCurrentUser={setCurrentUser}
          isPublicKeyForCurrentUser={isPublicKeyForCurrentUser}
          passkeysArr={passkeysArr}
          setResultCreatePasskey={setResultCreatePasskey}
          setPasskeysArr={setPasskeysArr}
        />
      )}
      {!currentUser && isAuthPasskey && !userOpenAuthPasskey && passkeysArr.length > 0 && (
        <button className='autn-passkey-btn' onClick={() => setUserOpenAuthPasskey(true)}>
          Авторизоваться по passkey
        </button>
      )}
      {!currentUser && isAuthPasskey && userOpenAuthPasskey && (
        <button className='autn-passkey-btn' onClick={() => setUserOpenAuthPasskey(false)}>
          Авторизоваться по логину и паролю
        </button>
      )}
      {/* Временная информация по авторизации */}
      {resultCreatePasskey && <p style={{ wordBreak: 'break-word' }}>{resultCreatePasskey}</p>}
      {resultAuthPasskey && (
        <p style={{ wordBreak: 'break-word', color: 'red' }}>{resultAuthPasskey}</p>
      )}
    </div>
  )
}

export default App
